.page-header {
  position: relative;

  display: flex;
  flex-direction: column;
}

.page-header__logo-link {
  position: relative;

  @include underline-grey;
}

.page-header__logo {
  margin-top: 11px;
  margin-bottom: 12px;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-header__logo--index {
  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 1;
  }
}

.main-nav__toggle--open {
  position: absolute;
  top: 21px;
  right: 12px;

  padding: 15px;

  background-color: transparent;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  border: none;
}

.main-nav__toggle--close {
  position: absolute;
  top: 23px;
  right: 12px;

  padding: 15px;

  background-color: transparent;
  background-image: url("../img/menu-mobile.svg");
  background-repeat: no-repeat;
  border: none;
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  font-size: 20px;
  text-align: center;
  text-transform: uppercase;

  @include reset-list;
}

.site-list__item {
  position: relative;

  padding-top: 18px;
  padding-bottom: 18px;

  @include underline-grey;
}

.site-list__link {
  font-weight: 600;
  color: $basic-black;
  text-decoration: none;
}

@media (min-width: $tablet-width) {
  .page-header {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .page-header__logo-link {
    width: 174px;
    height: 52px;
    margin-top: 22px;

    @include underline-none;
  }

  .main-nav__toggle--open {
    position: absolute;

    opacity: 0;
  }

  .main-nav__list {
    flex-direction: row;
    margin-top: 23px;

    font-size: 18px;
    text-align: left;
  }

  .site-list__item {
    margin-right: 35px;

    border-top: none;

    @include underline-none;

    &:last-child {
      margin-right: 0;

      border: none;
    }

    .site-list__link {
      font-weight: 400;
    }
  }

  .site-list__link {
    padding-bottom: 8px;

    &:hover {
      color: $special-black;
    }

    &:active {
      color: $special-light-black;
    }

    &:focus {
      color: $basic-black;

      border-bottom: 2px solid $basic-green;
    }
  }
}

@media (min-width: $desktop-width) {
  .page__body--index {
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: 95% top;
    background-size: 720px 696px;
    border-bottom: none;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/index-background-desktop@2x.jpg");
    }
  }

  .site-list__link {
    padding-bottom: 8px;

    color: $basic-black;
    text-decoration: none;

    &:hover {
      color: $special-black;
    }

    &:active {
      color: $special-light-black;
    }

    &:focus {
      color: $basic-black;

      border-bottom: 2px solid $basic-green;
    }
  }

  .page-header__logo-link {
    height: 59px;
    margin-top: 43px;
    margin-left: 0;
  }

  .main-nav__list {
    margin-top: 48px;
    margin-right: 8px;
    padding-bottom: 20px;

    font-size: 20px;
  }

  .site-list__link--white {
    padding-bottom: 8px;

    color: $basic-white;

    &:hover {
      color: $special-light-white;
    }

    &:active {
      color: $special-white;
    }

    &:focus {
      color: $basic-white;

      border-bottom: 2px solid $basic-white;
    }
  }
}
